const FunctionForm = (props) => {
  return (
    <div>FunctionFrom</div>
  );
}

const capitalize = (str) => {
  return str.charAt(0).toUpperCase()+str.slice(1);
}

const Home = (props) => {

  let initialState = {
    inputs: [],
    displayQuestions: false,
  };
  const [data, setData] = useState(initialState);
  const [abiData, setAbiData] = useState({});
  let abiParser = {};

  // const [form, setForm] = useState({});

  //const [showForm, setShowForm] = useState(false);
  const [formData, setFormData] = useState({display:false});

  function handleClickedLi (index, item) {
    console.log(
      'handleClickedLi:',
      index,
      item,
    );
    alert('handleClickedLi');
  }
 
  function handleInputOnChange(e, name, input) {
    e.preventDefault();
    formData.functionName = input;
    formData.values[name] = e.target.value;
    setFormData(formData);
    console.log("form.values:", formData.values);
  }

  async function handleSubmit(e, inputs, stateMutability) {
    e.preventDefault();

    if (stateMutability === 'payable') {
      alert('payable function add payable amount field');
    }

    // await timelockControllerSetup(abiData);
    // const returnData = await executeContractFunction (formData.name, formData.values, inputs, stateMutability);

    await propertiesSetup(abiData);
    const returnData = await executeContractFunctionProperties (formData.name, formData.values, inputs, stateMutability);
    console.log('executeContractFunction :: ', returnData);
    alert('executeContractFunction :: ' + returnData);

    formData.values.role = '';
    formData.values.account = '';
    formData.display = false;
    setFormData(formData);
  }

  useEffect(() => {
    async function setup () {
      const abiParser = new AbiParser();
      setAbiData(abiParser);
      console.log(
        'GetAbi IN Home:',
        GetAbi(abiParser)
      );
      setData(
        GetAbi(abiParser)
      );
      console.log(data);
    }
    setup();
    myEmitter.on('eventShowForm', (e) => {
      console.log('eventShowForm :: e', e.inputs);
      setFormData({
        display: true,
        name: e.name,
	inputs: e.inputs,
	stateMutability: e.stateMutability,
        values: { 
        },
      });
    });
  }, []);

  return (
    <div className="container mt-4 p-5 text-primary">

      <Card border="secondary" className="text-secondary">
        <Card.Header>
          Contract: { abiData.contractName }<br/>
	  Address: { abiData.address }
	</Card.Header>
	<Row>
	  <Col>
            <FormatAbi
              abi={abiData.abi}
              name={abiData.contractName}
              address={abiData.address}
              myEmitter={myEmitter}
            />
	  </Col>
	  <Col>
	    {formData.display && (
	      <Form onSubmit={(e) => handleSubmit(e, formData.inputs, formData.stateMutability)} className="p-5">
		<h3>Function: { capitalize(formData.name) } ({ formData.stateMutability })</h3>
		{ formData.inputs.map((input, index) => {
	          // return (<p><input value={input.name} /></p>);
	          return (
	            <Form.Group className="mb-3" controlId="formBasicEmail">
	              <Form.Label>
                        {capitalize(input.name)} ({input.type})
                      </Form.Label>
		      <Form.Control
                        required
                        name={input.name}
			value={formData.values[input.name]}
                        type={input.type}
                        onChange={(e) => handleInputOnChange(e, input.name, input)}
                        placeholder={"Enter " + input.name}
                      />
                    </Form.Group>
		  );
		})}
		{(formData.stateMutability === 'payable') && (
	          <Form.Group className="mb-3" controlId="formBasicEmail">
	            <Form.Label>
                      {capitalize('payment amount')}
                    </Form.Label>
		    <Form.Control
                      required
                      name="payment_value"
                      value={formData.values["payment_value"]}
                      type="float"
                      onChange={(e) => handleInputOnChange(e, "payment_value", formData.name)}
                      placeholder="Enter payment value" 
                    />
                  </Form.Group>
		)}
	        <Button type='submit' variant="outline-primary">Submit</Button>
	      </Form>
	    )}
	  </Col>
	</Row>
      </Card>

    </div>
  );
}

export default Home;
