import React, { useState, useEffect } from 'react';
import { useTable } from "react-table";
import {
	  BigNumber,
	  ethers,
	  utils,
} from "ethers";
import {
	  Button,
	  Card,
	  Col,
	  Form,
	  Row,
} from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";

const propertiesData = {};
const propertiesSetup = async (abiData) => {
  const provider = new ethers.providers.Web3Provider(window.ethereum);
  const address = await provider.send("eth_requestAccounts", []);
  const signer = provider.getSigner();
  const contract = new ethers.Contract(
    // addressPropertiesRinkeby,
    abiData.address,
    abiData.abi,
    provider
  );
  propertiesData.contract = contract; 
  propertiesData.contractWithSigner = contract.connect(signer);
  propertiesData.abiData = abiData;
}

const timelockControllerData = {};

const timelockControllerSetup = async (abiData) => {
  const provider = new ethers.providers.Web3Provider(window.ethereum);
  const address = await provider.send("eth_requestAccounts", []);
  const signer = provider.getSigner();
  const contract = new ethers.Contract(
    // addressPropertiesRinkeby,
    abiData.address,
    abiData.abi,
    provider
  );
  timelockControllerData.contract = contract; 
  timelockControllerData.contractWithSigner = contract.connect(signer);
  timelockControllerData.abiData = abiData;
}

const executeContractFunctionProperties = async (functionName, values, inputs, stateMutability) => {
  const args = [];
  inputs.map((input) => {
    if (input.type === 'uint256') {
      args.push(
        //ethers.utils.parseEther(values[input.name])
        values[input.name]
      );
    } else {
      args.push(values[input.name]);
    }
  });
  if (stateMutability === 'payable') {
    // console.log('payment_value:', values['payment_value']);
    const returnData = await propertiesData.contractWithSigner[functionName](
      ...args,
      {value: ethers.utils.parseEther(values['payment_value'])},
    );
    console.log(returnData);
    
    return('sending payable contract call');
  } else if (stateMutability === 'view' || stateMutability === 'pure') {
    const returnData = await propertiesData.contract[functionName](...args);
    console.log(
      "executeContractFunction - test CALL WITH REF :: ",
      returnData,
    );
    return returnData;
  } else {
    try {
      const returnData = await propertiesData.contractWithSigner[functionName](...args);
      console.log(
        "executeFunction - stateMutability not pure or view:",
        returnData,
      );
    }
    catch (e) {
      console.log("ERROR:", e);
      alert(e);
    }
  }
}

const executeContractFunction = async (functionName, values, inputs, stateMutability) => {

  const args = [];
  inputs.map((input) => {
    args.push(values[input.name]);
  });
  //console.log('executeContractFunction args:', args);
  
  if (stateMutability === 'view' || stateMutability === 'pure') {
    const returnData = await timelockControllerData.contract[functionName](...args);
    console.log(
      "executeContractFunction - test CALL WITH REF :: ",
      returnData,
    );
    return returnData;
  } else {
    const returnData = await timelockControllerData.contractWithSigner[functionName](...args);
    console.log(
      "executeFunction - stateMutability not pure or view:",
      returnData,
    );
  }
}

export {
  executeContractFunction,
  executeContractFunctionProperties,
  propertiesData,
  propertiesSetup,
  timelockControllerData,
  timelockControllerSetup,
};
