const timelockControllerData = {};

const timelockControllerSetup = async (abiData) => {
  const provider = new ethers.providers.Web3Provider(window.ethereum);
  const address = await provider.send("eth_requestAccounts", []);
  const signer = provider.getSigner();
  const contract = new ethers.Contract(
    // addressPropertiesRinkeby,
    abiData.address,
    abiData.abi,
    provider
  );
  timelockControllerData.contract = contract; 
  timelockControllerData.contractWithSigner = contract.connect(signer);
  timelockControllerData.abiData = abiData;
}

const executeContractFunction = async (functionName, values, inputs, stateMutability) => {

  const args = [];
  inputs.map((input) => {
    args.push(values[input.name]);
  });
  //console.log('executeContractFunction args:', args);
  
  if (stateMutability === 'view' || stateMutability === 'pure') {
    const returnData = await timelockControllerData.contract[functionName](...args);
    console.log(
      "executeContractFunction - test CALL WITH REF :: ",
      returnData,
    );
    return returnData;
  } else {
    const returnData = await timelockControllerData.contractWithSigner[functionName](...args);
    console.log(
      "executeFunction - stateMutability not pure or view:",
      returnData,
    );
  }
}

